.contact__title{
  font-size: 24px;
  line-height: 26px;
  text-transform: none;
  margin-top: 51px;
  margin-bottom: 0;
  position: relative;
  width: 52%;

  @media(min-width:$tablet){
    margin-top: 75px;
    font-size: 36px;
    line-height: 36px;
    width: 100%;
  }

  @media(min-width:$desktop){
    margin-top: 69px;
  }
}

.contact__title::after{
  content: "";
  position: absolute;
  pointer-events: none;
  width: 3000px;
  height: 2px;
  background: #68B738;
  top: 50%;
}

.contact__title--mail-phone::after{
  left: 194px;
  top: 30%;
  pointer-events: none;

  @media(min-width:$tablet){
    top: 50%;
    width: 3000px;
    left: 550px;
  }
}

.contact__title::before{
  pointer-events: none;

  @media(min-width:$tablet){
    content: "";
    position: absolute;
    pointer-events: none;
    width: 30px;
    height: 2px;
    background: #68B738;
    left: -67px;
    top: 50%;
  }

  @media(min-width:$desktop){
    width: 71px;
    left: -111px;
  }
}

.selection-programm__explanation{
  text-align: center;
  font-family: Arial;
  font-size: 14px;
  line-height: 16px;
  text-transform: none;

  @media(min-width:$tablet){
    text-align: left;
    font-size: 16px;
  }
}

.container-button-form{
  display: flex;
  flex-direction: column;
  margin-top: 41px;

  @media(min-width:$tablet){
    margin-top: 59px;
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  @media(min-width:$tablet){
    margin-top: 66px;
  }
}

.contact__list{
  display: flex;
  flex-direction: column;
  @media(min-width:$desktop){
    flex-direction: row;
  }
}
