.selection-programm{
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
}

.change-programm__container-fieldset{
  display: flex;
  flex-direction: column;

  @media(min-width:$desktop){
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 5.5%;
    margin-top: 66px;
  }
}
