.comment-textarea{
  border: 1px solid $grey-for-border;
  resize: none;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  padding: 9px 0 0 15px;
  width: 100%;
  box-sizing: border-box;
  margin-top: 37px;

  @media(min-width:$tablet){
    margin-top: 76px;
    font-size: 20px;
    line-height: 30px;
  }
}

.comment-textarea::placeholder{color:$special_dark-grey}

.contact__title--comment{
  margin-top: 23px;

  @media(min-width:$tablet){
    margin-top: 65px;
  }

  @media(min-width:$tablet){
    margin-top: 67px;
  }
}

.contact__title--comment::after{
  pointer-events: none;
  left: 142px;

  @media(min-width:$tablet){
    left: 219px;
  }
}
