.gift{
  background: url(../img/background/gift-photo-mobile.png) no-repeat;
  display: flex;
  flex-direction: column;
  margin: 57px auto 0;
  width: 280px;
  height: 289px;

  @media (min-resolution: 2dppx) {
    background:url(../img/background/gift-photo-mobile\@2.png) no-repeat;
  }

  @media(min-width:$tablet) and (max-width:$underdesktop){
    background: url(../img/background/gift-photo-tablet.png) no-repeat;
    flex-direction: row;
    width: 708px;
    height: 200px;

    @media (min-resolution: 2dppx) {
      background:url(../img/background/gift-photo-tablet\@2.png) no-repeat;
    }
  }

  @media(min-width:$desktop){
    width: 245px;
    height: 289px;
    margin: 0 0 0 80px;
}
}

.gift__text{
  width: 60%;
  margin: 48px auto 0;
  color: $basic-white;
  text-align: center;
  font-family: Arial;
  font-size: 16px;
  line-height: 20px;

  @media(min-width:$tablet){
    margin: 71px 0 0 265px;
    text-align: left;
}

@media(min-width:$desktop){
  margin: 46px auto 0;
  text-align: center;
}
}

.gift__icon{
  margin: 58px auto 0;

  @media(min-width:$tablet){
    margin-left: 78px;
    text-align: left;
    width: 80px;
    height: 82px;
}
}
