.contact__title--optionally{
  margin-top:22px;

  @media(min-width:$tablet){
    margin-top: 65px;
  }

  @media(min-width:$desktop){
    margin-top: 67px;
  }
}

.contact__title--optionally::after{
  pointer-events: none;
  left: 163px;

  @media(min-width:$tablet){
    left:249px;
  }
}

.optionally__list{
  margin-top: 39px;

  @media(min-width:$tablet){
    margin-top: 62px;
  }
}

.optionally__list{
  display: flex;
  flex-direction: column;
  text-transform: uppercase;
  row-gap: 20px;
  margin-left: 48px;

  @media(min-width:$tablet){
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto auto;
    row-gap: 42px;
    font-size: 20px;
    line-height: normal;
    margin-left: 55px;
  }

  @media(min-width:$desktop){
    grid-template-columns: 1fr 1fr 1fr 1fr;
    row-gap: 0;
  }
}

.optionally__item--checkbox+label{
  position: relative;
}

.optionally__item--checkbox+label::before{
  content: "";
  position: absolute;
  left: -48px;
  top: -5px;
  width: 30px;
  height: 30px;
  border: 1px solid $special_grey;
  border-radius: 5%;

  @media(min-width:$tablet){
    left: -55px;
    width: 40px;
    height: 40px;
  }
}

  .optionally__item--checkbox:checked+label::after{
    pointer-events: none;
    content: "";
    position: absolute;
    top: -2px;
    left: -45px;
    width: 26px;
    height: 26px;
    background: url(../img/background/tick.svg) no-repeat;
    z-index: 1;

    @media(min-width:$tablet){
      top: 2px;
    }
  }

.filter-input-radio+label{
  position: relative;
}





