.footer{
  padding: 39px 19px 32px;
  background-color:$basic-grey;
  display: flex;
  flex-direction: column;

  @media(min-width:$tablet){
    padding: 60px 30px 66px;
    flex-direction: row;
    justify-content: space-between;
  }

  @media(min-width:$desktop){
    padding: 59px 110px 56px;
  }
}

.footer__text-logo{
  text-align: center;
}

.footer__list-social{
  display: flex;
  margin-top: 56px;
  justify-content: center;
  column-gap: 34px;
  align-items: center;
  @media(min-width:$tablet){
    margin-top: 0;
    margin-bottom: 0;
  }
}

.footer__copyright{
  margin-top: 52px;
  color: $special_dark-grey;
  font-family: Arial;
  font-size: 16px;
  line-height: normal;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media(min-width:$tablet){
    margin-top: 0;
  }
}

.footer__copyright--logo{
  @media(min-width:$tablet){
    margin-left: 22px;
  }
}

.footer__text-logo,
.footer__list-social{
  position: relative;
}

.footer__text-logo::before,
.footer__list-social::before{
  display: inline-block;
  content: "";
  position: absolute;
  pointer-events: none;
  height: 1px;
  width: 100%;
  background-color: $border-grey;
  bottom: -32px;
  left: 0;

  @media(min-width:$tablet){
    display: none;
  }
}

.footer__list-social::before{
  pointer-events: none;
  bottom: -21px;
}

.social-item-icon:hover,
.footer__copyright--logo-svg:hover{
  fill:$basic-green;
}

.social-item-icon:active,
.footer__copyright--logo-svg:active{
  fill:$back-green-opacity6;
}

.social-item-icon--fb:hover{
  fill: $basic-grey;
  stroke:$basic-green;
}

.social-item-icon--fb:active{
  fill:$basic-grey;
  stroke:$back-green-opacity6;
}

.footer__text-logo--icon:hover,
.footer__copyright:hover{
  opacity: 0.8;
}

.footer__text-logo--icon:active,
.footer__copyright:active{
  opacity: 0.6;
}


.footer__copyright--logo-svg{

  @media(min-width:$tablet){
    margin-left: 22px;
  }
}
