.button{
  background-color: $basic-green;
  color: $basic-white;
  text-align: center;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
}

.button--catalog{
  width: 280px;
  padding: 10px 0;
  grid-column: 1/-1;
  margin: 0 auto;

  @media(min-width:$tablet){
    padding: 14px 26px;
    width: 199px;
  }

  @media(min-width:$desktop){
    width: 116px;
  }
}

.button--show-else{
  padding: 10px 94px;
  color: $special_dark-grey;
  font-size: 16px;
  line-height: 20px;
  margin: 0 auto;

  @media(min-width:$tablet){
    padding: 14px 26px;
    width: 199px;
  }

  @media(min-width:$desktop){
    width: 116px;
  }
}

li .button--show-else:hover{
  background-color: $grey-show-else;

}

li .button--show-else:active{
  color: $special-dark-grey-2opacity3;
}

.button--additional-products__item{
  padding: 8px 0 8px;
  width: 280px;
  margin: 0 auto;

  @media(min-width:$tablet){
    width: 632px;
    height: 31px;
    margin: 0 auto;
    grid-column: 1/-1;
    // grid-row: 2/3;
    padding: 8px 0 0;
  }

  @media(min-width:$desktop){
    width: 192px;
    height: 24px;
    grid-column: 3/4;
    margin-left: 106px;
  }
}

.selection-programm__form-button{
  padding: 8px 0 8px;
  width: 280px;
  margin: 0 auto;
  font-size: 16px;
  line-height: 20px;

  @media(min-width:$tablet){
    padding: 14px 0 0;
    width: 282px;
    height: 38px;
    font-size: 20px;
    line-height: 20px;
    margin-left: 0;
  }

  @media(min-width:$desktop){
    width: 568px;
  }
}

.button:hover{
  background-color: $basic-green-2;
}

.button:active{
  color: $basic-white-opacity6;
  background-color: $basic-green-2;
}
