
.direction-programm__item{
  background-color: $basic-grey;
  padding-top: 21px;
  padding-bottom: 18px;
  position: relative;

  @media(min-width:$tablet){
    padding-top: 65px;
    padding-bottom: 57px;
  }

  @media(min-width:$desktop){
    padding-top: 54px;
    padding-bottom: 59px;
  }
}

.direction-programm__wrapper{
  padding: 0 20px;
  @media(min-width:$tablet){
    padding: 0 30px;
  }
  @media(min-width:$desktop){
    padding: 0 110px;
  }
}

.direction-programm__list{
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  margin: 0;
  @media(min-width:$desktop){
    row-gap: 30px;
  }
  @media(min-width:$desktop){
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 80px;
    row-gap: 0;
  }
}

.direction-programm__title{
font-size: 24px;
line-height: 37px;
text-transform: uppercase;
display: flex;
align-items: center;
margin-left: 91px;
position: relative;
@media(min-width:$tablet){
  margin-left: 53px;
}
@media(min-width:$desktop){
  margin-left: 214px;
}
}

.direction-programm__title::before{
  content: "";
  position: absolute;
  top: -7px;
  left: -70px;
  width: 50px;
  height: 50px;
  background: $basic-green;
  border-radius: 50%;
  pointer-events: none;

  @media(min-width:$tablet){
    top: -25%;
    left: auto;
    right: 62px;
    width: 200px;
    height: 211px;
    background: url(../img/cat_back.svg) no-repeat;
    border-radius: 0;
  }

  @media(min-width:$desktop){
    background: $basic-green;
    width: 100px;
    height: 100px;
    top: -28px;
    left: -166px;
    border-radius: 50%;
  }
}

.direction-programm__title--pro::before{
  pointer-events: none;

  @media(min-width:$tablet) and (max-width:$underdesktop){
    top: -25%;
    left: auto;
    right: 27px;
    width: 268px;
    height: 200px;
    background: url(../img/cat_back--pro.svg) no-repeat;
    border-radius: 0;
  }
}

.direction-programm__title::after{
  content: "";
  position: absolute;
  pointer-events: none;
  width: 100%;
}

.direction-programm__title--slim::after{
  background: url(../img/cat_slim.svg) no-repeat;
  height: 150%;
  top: -10px;
  left: -65px;
  pointer-events: none;

  @media(min-width:$tablet){
    background-size: 140px auto;
    height: 540%;
    top: -15px;
    left: 68%;
    background: none;
  }

  @media(min-width:$desktop){
    background: url(../img/cat_slim.svg) no-repeat;
    background-size: 70px;
    top: -31px;
    left: -148px;
  }
}

.direction-programm__title--pro::after{
  background: url(../img/cat_pro.svg) no-repeat;
  height: 100%;
  top: 3px;
  left: -78px;
  pointer-events: none;

  @media(min-width:$tablet){
    background-size: auto 112px;
    height: 328%;
    top: 50px;
    left: 60%;
    background: none;
  }
  @media(min-width:$desktop){
    background: url(../img/cat_pro.svg) no-repeat;
    background-size: 134px;
    top: -11px;
    left: -183px;
  }
}


.direction-programm__text{
color: $special_dark-grey;
font-family: Arial;
font-size: 14px;
line-height: 18px;
margin-top: 29px;
margin-left: 20px;
margin-bottom: 38px;
width: 80%;

@media(min-width:$tablet){
  font-size: 16px;
  line-height: 24px;
  margin-left: 52px;
  margin-top: 5px;
  margin-bottom: 52px;
  width: 55%;
}

@media(min-width:$desktop){
  margin-top: 54px;
  margin-bottom: 27px;
  width: 84%;
}
}

.direction-programm__text--pro{

  @media(min-width:$tablet){
    width: 50%;
  }

  @media(min-width:$desktop){
    width: 81%;
  }
}

.direction-programm__link{
  position: relative;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  margin-left: 20px;
  @media(min-width:$tablet){
    margin-left: 52px;
    font-size: 20px;
    line-height: normal;
  }
}

.direction-programm__link:active{
  opacity: 0.3;
}

.direction-programm__link::after{
  content: "";
  position: absolute;
  left: 100px;
  top: 6px;
  width: 100%;
  height: 100%;
  background: url(../img/icon_arrow.svg) no-repeat;
  pointer-events: none;

  @media(min-width:$tablet){
    left: 133px;
    top: 9px;
  }

  @media(min-width:$desktop){
    left: 139px;
  }
}

.direction-programm__picture{
  margin-right: 19px;
}
