.contact__list{
  display: flex;
  flex-direction: column;
  row-gap: 14px;

  @media(min-width:$tablet){
    row-gap: 20px;
  }

  @media(min-width:$desktop){
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 5.5%;
  }
}

.contact__item{
  color: var(--special-dark-grey, #444);
  display: flex;
  align-items: center;
  column-gap: 3.5%;
  position: relative;
}

.contact__item--mail::before{
    content: "";
    position: absolute;
    left: auto;
    right: 15px;
    width: 20px;
    height: 15px;
    background: url(../img/background/icon_email.svg) no-repeat;
    pointer-events: none;
    display: inline-block;
  }

.contact__item--phone::before{
    content: "";
    position: absolute;
    left: auto;
    right: 15px;
    width: 20px;
    height: 21px;
    right: 13px;
    background: url(../img/background/icon_phone.svg) no-repeat;
    pointer-events: none;
    display: inline-block;
  }

.contact__item-input{
  border: 1px solid $grey-for-border;
  width: 100%;
  padding-top: 13.2px;
  padding-bottom: 14.8px;
  padding-left: 14.2px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  // position: relative;

  @media(min-width:$tablet){
    font-size: 20px;
    line-height: normal;
  }
}

.contact__item-input::placeholder{color:$special_dark-grey}

.contact__item-label--email{
  width: 73px;
}

.contact__form{
  margin-top: 35px;

  @media(min-width:$tablet){
    margin-top: 76px;
  }
}
