.instruction{
  padding: 27px 30px 42px;
  @media(min-width:$tablet){
    padding-right: 45px 30px 147px;
  }

  @media(min-width:$desktop){
    padding: 71px 110px 126px;
  }
}

.instruction__title{
  font-size: 36px;
  line-height: 40px;
  margin: 0;
  grid-column: 1/-1;
}

.instruction__list{
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  margin-top: 40px;
  @media(min-width:$tablet){
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    column-gap: 60px;
  }

  @media(min-width:$desktop){
    display: flex;
    flex-direction: row;
    column-gap: 80px;
    justify-content: space-between;
  }
}

.instruction__item{
  display: flex;
  column-gap: 20px;

  @media(min-width:$tablet){
    row-gap: 31px;
    flex-direction: column;
    position: relative;
    min-height: 232px;
  }

}

.instruction__item::before{
  @media(min-width:$tablet){
    content: "";
    pointer-events: none;
    width: 100%;
    height: 100%;
    left: 102px;
    position: absolute;
    top: 0;
    z-index: -1;
  }
}

.instruction__item--1::before{
  pointer-events: none;
  background: url(../img/1.svg) no-repeat;
}
.instruction__item--2::before{
  pointer-events: none;
  background: url(../img/2.svg) no-repeat;
}
.instruction__item--3::before{
  pointer-events: none;
  background: url(../img/3.svg) no-repeat;
}
.instruction__item--4::before{
  pointer-events: none;
  background: url(../img/4.svg) no-repeat;
}

.instruction__text{
  font-family: "Arial";
  font-size: 14px;
  line-height: 18px;
  margin: 0;
  color: $special_dark-grey;

  @media(min-width:$tablet){
    font-size: 16px;
    line-height: 24px;
  }

}
