.catalog__list{
  padding: 11px 18px 19px 20px;
  display: flex;
  flex-direction: column;
  margin: 0;
  align-items: center;

  @media(min-width:$tablet){
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 11%;
    justify-items: center;
    padding-top:0;
    margin-top: 123px;
    row-gap: 140px;
  }

  @media(min-width:$desktop){
    grid-template-columns: 1fr 1fr 1fr 1fr;
    column-gap: 5.5%;
    padding-bottom: 0;
  }
}

.catalog__title{
  color: $special-dark-grey-2;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  grid-column: 2/4;
  grid-row:1/2;
  text-align: left;
  margin-top: 44px;
  width: 55%;

  @media(min-width:$tablet){
    text-align: center;
    margin-top: 161px;
    width: 39%;
    font-size: 20px;
    line-height: 24px;
  }

  @media(min-width:$desktop){
    line-height: 21px;
  }
}

.catalog__table{
  color: $special_dark-grey;
  text-align: center;
  font-family: Arial;
  font-size: 12px;
  line-height: 16px;
  margin-bottom: 22px;
  grid-column: 2/3;
  grid-row: 2/3;

  @media(min-width:$tablet){
    width: 248px;
    font-size: 16px;
    line-height: 16px;
    margin-bottom: 33px;
  }

  @media(min-width:$desktop){
    width: 168px;
  }
}

.catalog__table-first-column{
  text-align: left;
}

.catalog__table-second-column{
  text-align: right;
}

.catalog__item{
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: min-content min-content min-content;
  align-items: center;
  position: relative;
  width: 282px;

  @media(min-width:$tablet){
    position: relative;
    min-height: 430px;
    width: 324px;
    display: flex;
    flex-direction: column;
    background: $basic-grey;
  }

  @media(min-width:$desktop){
    width: 245px;
  }
}



.page__title--catalog{
  margin: 27px 0 41px 20px;
  @media(max-width:$tablet){
    position: relative;
  }
}

.page__title--catalog::before,
.catalog__item::before{
  @media(max-width:$tablet){
    display: inline-block;
    content: "";
    position: absolute;
    pointer-events: none;
    height: 1px;
    width: 768px;
    background-color: $border-grey;
    bottom: -25px;
    left: -256px;
  }
}

.page__title--catalog::before{
  @media(max-width:$tablet){
    left: -40px;
  }
}

.page__title--catalog::before{
  @media(max-width:$tablet){
    bottom: -42px;
  }
}

.catalog__table-row{
  position: relative;
}

.catalog__table-row::before{
  @media(min-width:$tablet){
    display: inline-block;
    content: "";
    position: absolute;
    pointer-events: none;
    height: 1px;
    width: 100%;
    background-color: $border-grey;
    bottom: 0;
  }
}


.catalog__picture{
  grid-column: 1/2;
  grid-row: 1/3;
  margin: 0 auto;

  @media(min-width:$tablet){
    position: absolute;
    top: -45px;
    left: 83px;
  }

  @media(min-width:$desktop){
    left: 44px;
  }
}

.catalog__picture--even{
  @media(min-width:$tablet){
    top: -75px;
  }
}

.catalog__item--show-else{
  display: flex;
  flex-direction: column;

  @media(min-width:$tablet){
    min-height: 430px;
    border: 2px solid #F2F2F2;
    width: 324px;
  }

  @media(min-width:$desktop){
    width: 245px;
  }
}

.catalog__title--show-else{
  color: $black1;
  text-align: center;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  margin-top: 103px;
  position: relative;

  @media(min-width:$tablet){
    margin: 161px auto 0;
    width: 44%;
    font-size: 20px;
    line-height: 22px;
  }
}

.catalog__title--show-else::before{
  position: absolute;
  top: -45px;
  left: 45%;
  background: url(../img/plus.svg) no-repeat;
  content: "";
  width: 30px;
  height: 30px;
  pointer-events: none;

  @media(min-width:$tablet){
    top: -85px;
  }

  @media(min-width:$desktop){
    left: 35%;
  }
}

.catalog__text--show-else{
  color:$special_dark-grey;
  text-align: center;
  font-family: Arial;
  font-size: 12px;
  line-height: 16px;
  margin: 8px auto 21px;

  @media(min-width:$tablet){
    margin: 23px auto 60px;
    width: 52%;
    font-size: 16px;
    line-height: 18px;
  }

  @media(min-width:$tablet){
    width: 68%;
    margin: 23px auto 32px;
  }
}

.button--show-else{
  background-color: $basic-grey;
}
