.cat-data__legend{
  font-size: 14px;
  margin-top: 37px;
  line-height: 18px;

  @media(min-width:$tablet){
    margin-top: 54px;
    font-size: 20px;
    line-height: 30px;
  }

  @media(min-width:$desktop){
    margin-top: 56px;
  }
}

.cat-data__item{
  color: var(--special-dark-grey, #444);
  display: flex;
  align-items: center;
  column-gap: 3.7%;
}

.change-programm__item{
  color: var(--special-dark-grey-2, #222);
  line-height: 18px;
}

.cat-data__item-input{
  border: 1px solid $grey-for-border;
  width: 100%;
  padding-top: 13.2px;
  padding-bottom: 14.8px;
  padding-left: 14.2px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.cat-data__item-input::placeholder{color:$special_dark-grey}

.cat-data__list{
  display: flex;
  flex-direction: column;
  row-gap: 13px;
}

.cat-data__form-info{
  margin-top: 39px;

  @media(min-width:$tablet){
    margin-top:66px ;
  }

  @media(min-width:$desktop){
    margin-top: 0;
  }
}

.cat-data__item-label--age{
  min-width: 94px;
}

.cat-data__item-label--weight{
  min-width: 60px;
}
