.partnership{
  display: grid;
  column-gap: 2px;
  padding: 28px 19px;
  grid-template-columns: 1fr 1fr;

  @media(min-width:$tablet){
    justify-content: space-between;
    column-gap: 0;
    display: flex;
    padding: 66px 72px 51px 71px;
  }

  @media(min-width:$desktop){
    padding:62px 89px 51px 76px;
  }
}

.partnership--catalog-select{
  background: $back-grey;

  @media(min-width:$desktop){
    background: $basic-white;
  }
}

.partnership__title{
  color: #111;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  font-family: "Oswald";
  margin: 0;
}

.partnership__text{
  color: $special_dark-grey;
  font-family: Arial;
  font-size: 14px;
  line-height: 20px;
  font-style: normal;
  width: 140px;

  @media(min-width:$tablet){
    text-align: right;
    width: 159px;
  }

  @media(min-width:$desktop){
    text-align: left;
  }
}

.page-main__partnership{

  @media(min-width:$desktop){
    position: absolute;
    z-index: 1;
    width: 570px;
    height: 201px;
    background: $basic-white;
    margin-left: 110px;
    margin-top: 95px;
  }
}


.partnership__text-street{
  padding: 0;
  margin: 0;

  @media(min-width:$tablet){
    padding-top: 20px;
  }
}
