.target__list{
  display: flex;
  flex-direction: column;
  row-gap: 24px;
  margin-left: 45px;

  @media(min-width:$tablet){
    border: 1px solid #E7E7E7;
    padding: 30px 0 34px 88px;
    margin-left: 0;
  }
}

.target__item{
  display: flex;
  column-gap: 5%;
  color: $special-dark-grey-2;

  @media(min-width:$tablet){
    font-size: 20px;
    line-height: normal;
  }
}

.target__item label {
  display: block;
  cursor: pointer;
  user-select: none;
  position: relative;
}

.target__form{
  margin-top: 52px;

  @media(min-width:$tablet){
    margin-top: 102px;
  }

  @media(min-width:$desktop){
    margin-top: 0px;
  }
}



.filter-input-radio+label::before {
  content: "";
  position: absolute;
  left: -45px;
  top: -9px;
  width: 30px;
  height: 30px;
  border: 1px solid $special_grey;
  border-radius: 50%;

  @media(min-width:$tablet){
    width: 40px;
    height: 40px;
    left: -61px;
  }
}

.filter-input-radio:checked+label::after {
  content: "";
  position: absolute;
  top: 0px;
  left: -36px;
  width: 15px;
  height: 15px;
  background-color: $basic-green;
  border-radius: 50%;

  @media(min-width:$tablet){
    width: 18px;
    height: 18px;
    left: -49px;
    top: 4px;
  }
}
