.logo{
  display: flex;
  justify-content: space-between;
  padding: 0 22px 16px 22px;
  align-items: center;
  position: relative;

  @media(min-width:$tablet){
    padding: 0 0 0;
  }

  @media(min-width:$desktop){
    padding: 0 0 0;
  }
}

.logo:hover{
  opacity: 0.8;
}

.logo:active{
  opacity: 0.6;
}


.logo::before{
  display: inline-block;
  content: "";
  position: absolute;
  pointer-events: none;
  height: 1px;
  width: 100%;
  background-color: $border-grey;
  bottom: 0;
  left: 0;

  @media(min-width:$tablet){
    display: none;
  }
}

.header{
  padding: 12px 0 0;

  @media(min-width:$tablet){
    display: flex;
    padding: 24px 29px 0;
    justify-content: space-between;
    align-items: center;
  }

  @media(min-width:$desktop){
    padding: 53px 110px 0;
  }
}

.logo__burger{
  display: none;
}

.logo__desc{
  @media(min-width:$tablet){
    display: none;
  }
}

.logo__exit{
  @media(min-width:$tablet){
    display: none;
  }
}

