.additional-products__head-title{
  color: $basic-black;
  text-align: center;
  font-size: 24px;
  line-height: 24px;
}

.additional-products__title--item{
  color: $special-dark-grey-2;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  margin-bottom: 13px;
  margin-top: 31px;

  @media(min-width:$tablet){
    margin-top: 25px;
  }
}

.additional-products__table-row{
  color: $special_dark-grey;
  font-family: Arial;
  font-size: 14px;
  line-height: 14px;
  margin-bottom: 15px;

  @media(min-width:$tablet){
    margin-bottom: 0px;
  }

  @media(min-width:$tablet) and (max-width:$underdesktop){
    display: flex;
    justify-content: flex-end;
    column-gap: 15%;
  }
}

.additional-products__table{
    margin-bottom: 15px;
  @media(min-width:$tablet){
    margin-bottom: 0px;
  }
}

.additional-products__item{
  display: flex;
  flex-direction: column;
  position: relative;

  @media(min-width:$tablet){
    min-height: 139px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    align-items: baseline;
  }

  @media(min-width:$desktop){
    grid-template-columns: 1fr 1fr 1fr;
    min-height: 72px;
    grid-template-rows: 1fr;
  }
}

.additional-products__title{
  position: relative;
  margin-bottom: 18px;
  font-size: 24px;
  line-height: 24px;

  @media(min-width:$tablet){
    margin-left: 37px;
    margin-bottom: 80px;
  }

  @media(min-width:$desktop){
    grid-column: 1/-1;
    margin-left: 0;
  }
}

.additional-products__title::after{
  content: "";
  position: absolute;
  pointer-events: none;
  width: 448px;
  height: 2px;
  background: $basic-green;
  left: 246px;
  top: 50%;

  @media(min-width:$tablet){
    width: 3000px;
  }
}

.additional-products__title::before{

  @media(min-width:$tablet){
    content: "";
    position: absolute;
    pointer-events: none;
    width: 30px;
    height: 2px;
    background: $basic-green;
    left: -67px;
    top: 50%;
  }

  @media(min-width:$tablet){
    width: 71px;
    left: -111px;
  }
}

.additional-products__item::after{
  content: "";
  position: absolute;
  pointer-events: none;
  height: 1px;
  width: 100%;
  background-color: $special_grey;
  bottom: -19px;

  @media(min-width:$tablet){
    bottom: -1px;
    width: 708px;
    left: -38px;
  }

  @media(min-width:$desktop){
    width: 895px;
    left: 0;
  }
}

.additional-products__item--first::before{

  @media(min-width:$desktop){
    content: "";
    position: absolute;
    pointer-events: none;
    height: 1px;
    width: 100%;
    background-color: $special_grey;
    top: -1px;
    width: 895px;
    left: 0;
  }

}

.additional-products__list{

  @media(min-width:$tablet){
    width: 632px;
    margin: 0 auto 0;
  }

  @media(min-width:$desktop){
    width: 895px;
    margin: 0;
  }
}


.additional-products{
  @media(min-width:$tablet) and (max-width:$underdesktop){
    border: $special_grey solid 1px;
    margin: 0 auto;
    width: 708px;
 }
}
