a{
  text-decoration: none;
  color: inherit;
}

ul{
  list-style: none;
  padding: 0;
  margin: 0;
}

body{
  margin: 0;
}

.page-body{
  font-family: Oswald;
  font-weight: 400;
  color: $basic-black;
  font-style: normal;
  overflow-x: hidden;
}

label, input{
  font-family: Oswald;
}

fieldset{
  margin: 0;
  padding: 0;
}

.page-body--index{

  @media(min-width:$desktop){
    background: linear-gradient(180deg, rgba(104, 183, 56, 0.85) 100%, transparent 100.1%) no-repeat, url(../img/background/index-background-desktop.jpg) no-repeat;
    background-position: 100% 0;
    background-size: 50% 696px;
  }

}

h1,h2,h3,h4{
  font-weight: 400;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page__title{
  font-size: 36px;
  line-height: 36px;
  text-align: left;
  margin: 0;
  text-transform: none;

  @media(min-width:$tablet){
    font-size: 60px;
    line-height: 60px;
  }
}

.together-gift-additional-products{
  display: flex;
  flex-direction: column;
  padding: 25px 20px 35px;

  @media(min-width:$tablet){
    padding: 68px 30px 76px;
  }

  @media(min-width:$desktop){
    display: grid;
    grid-template-columns: 3fr 1fr;
    grid-template-rows: auto auto;
    padding: 75px 111px 86px;
  }

}

fieldset{
  border: none;
}


.page-main__wrapper-selectprogramm{
  padding: 27px 20px 32px;

  @media(min-width:$tablet){
    padding: 64px 69px 72px;
  }

  @media(min-width:$desktop){
    padding: 73px 110px 82px;
  }
}
