.lead{
  background: url(../img/background/lead-cat.jpg) no-repeat;
  background-position: top;
  color: $basic-white;
  padding-top: 27px;
  padding-bottom: 22px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;

  @media (min-resolution: 2dppx) {
    background:url(../img/background/lead-cat@2.jpg) no-repeat;
  }

  @media(min-width:$tablet){
    background:none;
    padding-top: 62px;
    padding-left: 117px;
    padding-bottom: 526px;
    align-items: start;
  }

  @media(min-width:$desktop){
    padding-top: 111px;
    padding-left: 190px;
    padding-bottom: 267px;
  }
}

.lead::after{
  position: absolute;
  background: linear-gradient(180deg, rgba(104, 183, 56, 0.85) 100%, transparent 100.1%);
  content: "";
  height: 302px;
  right: auto;
  left: auto;
  top: 0px;
  width: 320px;
  pointer-events: none;

  @media(min-width:$tablet){
    background:none;
  }
}

.lead__title{
  line-height: 36px;
  font-size: 36px;
  margin: 0;
  z-index: 1;

  @media(min-width:$tablet){
    color: $basic-black;
    margin-left: 6px;
  }

  @media(min-width:$desktop){
    margin-left: 0;
  }
}

.lead__text{
  line-height: 14px;
  text-transform: uppercase;
  font-size: 14px;
  margin-top: 25px;
  margin-bottom: 302px;
  z-index: 1;

  @media(min-width:$tablet){
    margin-top: 41px;
    margin-left: 6px;
    color: $basic-black;
    margin-bottom: 49px;
    font-size: 20px;
    line-height: 20px;
  }

  @media(min-width:$desktop){
    margin-bottom: 52px;
    margin-left: 0;
  }
}

.lead-picture-basket{
  position: absolute;
  top: 167px;
  z-index: 1;

  @media(min-width:$tablet){
    top: 216px;
    left: -19px;
    z-index: -1;
  }
  @media(min-width:$desktop){
    top: 24px;
    right: 0;
    left: 533px;
  }
}

.lead__button{
  padding: 8px 58px;

  @media(min-width:$tablet){
    padding: 14px 26px;
  }
}

.lead picture{
  display: flex;
  justify-content: center;
}


