.nav__list{
  display: flex;
  flex-direction: column;
  margin: 0;

  @media(min-width:$tablet){
    flex-direction: row;
  }
}

.nav-item{
  font-family: Oswald;
  font-size: 20px;
  font-weight: 500;
  line-height: 20px;
  text-transform: uppercase;
  color: $basic-black;
  padding: 22px 17px;
  position: relative;

  @media(min-width:$tablet){
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
  }

  @media(min-width:$desktop){
    font-size: 20px;
  }
}

.nav-item--index{

  @media(min-width:$desktop){
  color: $basic-white;
  }
}

ul .nav-item--index::before{
  pointer-events: none;

  @media(min-width:$desktop){
    background-color: $basic-white;
  }
}

.nav-item:hover{
  opacity: 0.6;
}

.nav-item:visited{
  opacity: 0.3;
}

.nav-item--current{
  position: relative;
}

.nav-item--current::before{
  pointer-events: none;

  @media(min-width:$tablet){
    position: absolute;
    content: " ";
    width: 86%;
    height: 2px;
    left: 7%;
    bottom: 10px;
    background-color: $basic-green;
  }

  @media(min-width:$desktop){
    bottom: 8px;
  }
}

.nav-item::after{
  display: inline-block;
  content: "";
  position: absolute;
  pointer-events: none;
  height: 1px;
  width: 100%;
  background-color: $border-grey;
  bottom: 0;
  left: 0;

  @media(min-width:$tablet){
    display: none;
  }
}

.nav{
  text-align: center;
}

.nav--closed{
  display: none;
}

.nav--opened{
  display: block;
}

.toggle--inactive{
  display: none;
}

.toggle--active{
  display: block;

  @media(min-width:$tablet){
    display: none;
  }
}

