.example{
  padding:27px 20px 25px;
  background-color: $back-grey;
  display: flex;
  flex-direction: column;

  @media(min-width:$tablet){
    padding:43px 30px 67px;
  }

  @media(min-width:$desktop){
    flex-direction: row;
    padding:0 110px 67px;
    justify-content: space-between;
  }
}

.example__desc-area{

  @media(min-width:$tablet) and (max-width:$underdesktop){
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-template-rows: auto auto auto;
  }
  @media(min-width:$desktop){
    width:50%;
  }
}

.example__title{
  font-size: 36px;
  line-height: 40px;
  margin-bottom: 0px;

  @media(min-width:$tablet) and (max-width:$underdesktop){
   grid-column: 1/-1;
  }
}

.example__list{
  display: flex;
  column-gap: 67px;
  justify-content: space-around;

  @media(min-width:$tablet) and (max-width:$underdesktop){
    justify-content: flex-start;
    column-gap: 130px;
    br{
      display: none;
    }
  }

  @media(min-width:$desktop){
    justify-content: flex-start;
    margin-top: 84px;
    column-gap: 129px;
  }
}

.example__text{
  font-family: "Arial";
  font-size: 14px;
  line-height: 18px;
  margin-top: 33px;
  color: $special_dark-grey;

  @media(min-width:$tablet) and (max-width:$underdesktop){
   grid-column: 1/-1;
   font-size: 16px;
   line-height: 24px;
   margin-top: 63px;
  }

  @media(min-width:$desktop){
    br{
      display: none;
    }
    margin-top: 146px;
  }
}

.example__head-item{
  text-align: center;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  margin: 0 auto;

  @media(min-width:$tablet){
    font-size: 20px;
    text-align: right;
    margin: 0 0 0 auto;
    width: 179px;
  }

  @media(min-width:$desktop){
    text-align: left;
    margin: 55px auto 0 0;
    width: 100%;
  }
}

.example__logo{
  margin-top: 44px;
  @media(min-width:$desktop){
    margin-top: -76px;
  }
}

.example__slider{
  margin-top: 13px;
  text-align: center;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  column-gap: 13px;
  align-items: center;
}

.example__slider-text{
  padding-top: 7px;
  padding-bottom: 7px;
}

.example__slider-text--current{
  text-decoration: underline dotted grey;
  text-underline-offset: 7px;
}

.example__title--table{
  font-size: 24px;
  line-height: 24px;
  text-align: center;
  margin-bottom: 13px;

  @media(min-width:$tablet){
    font-size: 30px;
    line-height: 37px;
    margin-top: 0;
    margin-bottom: 11px;
  }

  @media(min-width:$desktop){
    text-align: center;
  }
}

.example__text--table{
  color:$special_dark-grey;
  text-align: center;
  font-size: 12px;
  line-height: 12px;
  margin-top: 0px;
  width: 75px;

  @media(min-width:$tablet){
    width: 100%;
  }
}
.example__text--table-weight{
  width: 65px;
  margin-top: 0;
  text-align: center;

  @media(min-width:$tablet){
    width: 100%;
  }
}

.example__picture-area{
    text-align: center;

    @media(min-width:$desktop){
      min-width: 677px;
    }
}

.example__item{
  position: relative;
}

.example__item::before{
  position: absolute;
  content: "";
  pointer-events: none;
  width: 124px;
  height: 56px;
  top: 12px;
  background: url(../img/weight_back.svg) no-repeat;

  @media(min-width:$tablet){
    width: 162px;
    height: 72px;
    background: url(../img/weight_back-bigger.png);
    top: -15px;
  }

}

.example__item--weight{
  margin-left: 10px;

  @media(min-width:$tablet){
    margin-left: 36px;
  }
}

.example__item--weight::before{
  left:-30px;

  @media(min-width:$tablet){
    left:-36px;
  }
}
.example__item--days::before{
  left:-23px;

  @media(min-width:$tablet){
    left:-27px;
  }
}

